*{
    color: white;
   }
   .Slider{
       position: relative;
       /* border: 1px solid red; */
       width: 100%;
       height: 100%;
       box-sizing: border-box; 
       margin: 0;
       padding: 0;
       display: flex;
       align-items: center;
       overflow: hidden; 
       background: #78C47A;
   }
   
   .Slide{
        /* border: 10px solid blue;  */
       min-width: 100%;
       height: 100%;
       position: relative;
       transition: 0.5s;
       overflow: hidden;
       border: none;
   }
   
   
   
   .goLeft{
       position: absolute;
       top: 50%;
       left: 0;
       transform: translateY(-50%);
       width: 5%;
       height: 80%;
       background: none;
       border: none;
       outline: none;
       cursor: pointer;
       transition: 0.5s;
   }
   .goLeft:hover{
       background: rgba(0, 0, 0, 0.356);
   }
   
   .goRight{
       position: absolute;
       top: 50%;
       right: 0;
       transform: translateY(-50%);
       width: 5%;
       height: 80%;
       background: none;
       border: none;
       outline: none;
       cursor: pointer;
       transition: 0.5s;
   }
   
   .goRight:hover{
       background: rgba(0, 2, 0, 0.356);
   }


   @media (max-width: 920px){
    .goLeft{
        width: 20%;
    }
    .goRight{
        width: 20%;
    }
   }


   